import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core'
import { Product } from '@core/models/product.model'
import { ApiService } from '@core/services/api.service'
import { takeUntil } from 'rxjs'
import { AutoDestroy } from '@core/common/auto-destroy'

@Component({
  selector: 'app-product-promo-banner',
  templateUrl: './product-promo-banner.component.html',
  styleUrls: ['./product-promo-banner.component.scss']
})
/**
 * @todo replace product banner with this one
 */
export class ProductPromoBannerComponent extends AutoDestroy implements OnInit {

  @Input()
  public categoryId?: number

  @Output()
  public addToCart = new EventEmitter<any>()

  public product: Product

  constructor(private api: ApiService) {
    super()
  }

  ngOnInit(): void {
    this.getProduct()
  }

  public _addToCart() {
    this.addToCart.emit()
  }

  private getProduct() {
    this.api.get(`product/promo-product/${this.categoryId || 1}`)
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.product = data
      })
  }
}
