import { Component, OnInit } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { Product } from '@core/models/product.model'
import { AutoDestroy } from '@core/common/auto-destroy'
import { takeUntil } from 'rxjs'
import { ProductCategory } from '@core/models/types'
import { MicroserviceType as MsT } from '@core/enums/microservice-type.enum'

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent extends AutoDestroy implements OnInit {

  public saleProducts: Array<Product>
  public productFilters = ['New', 'Seasonal', 'Favorite', 'Last order', 'History']
  public activeFilter = this.productFilters[0]
  public categories: Array<ProductCategory>
  public heroBanners: Array<any>

  constructor(private api: ApiService) {
    super()
  }

  ngOnInit(): void {
    this.fetchSaleProducts()
    this.fetchProducts()
    this.fetchPopularCategories()
    this.fetchHeroBanners()
  }

  public fetchProducts(filter?: string) {
    this.activeFilter = filter || this.productFilters[0]

    this.api.get(`product/sale/${filter}`)
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.saleProducts = data.products
      })
  }

  private fetchSaleProducts() {
    this.api.get('product/sale/a')
      .subscribe(({ data }) => {
        this.saleProducts = data.products
      })
  }

  private fetchPopularCategories() {
    this.api.get('filter/popularCategories', MsT.inv)
      .pipe(takeUntil(this.destroy$))
      .subscribe((categories: Array<ProductCategory>) => {
        this.categories = categories.concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories).concat(categories)
      })
  }

  private fetchHeroBanners() {
    this.api.get('hero-banners')
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.heroBanners = data
      })
  }

}
