import { Component, Input, OnInit, ViewChild } from '@angular/core'
import { ProductCategory } from '@core/models/types'
import { SwiperComponent as OrigSwiperComponent } from 'swiper/angular/angular/src/swiper.component'
import SwiperCore, { FreeMode, Navigation } from 'swiper'
import { NavigationOptions } from 'swiper/types'

SwiperCore.use([FreeMode, Navigation])

@Component({
  selector: 'app-categories-swiper',
  templateUrl: './categories-swiper.component.html',
  styleUrls: ['./categories-swiper.component.scss']
})
export class CategoriesSwiperComponent implements OnInit {

  @ViewChild('swiper', { static: false }) swiper: OrigSwiperComponent

  @Input()
  public categories: Array<ProductCategory>

  @Input()
  public icon: string

  @Input()
  public title: string

  public navOpt: NavigationOptions = { nextEl: '.swiper-next', prevEl: '.swiper-prev', }

  constructor() { }

  ngOnInit(): void {
  }
}
